(function () {
  "use strict";

  angular
    .module("project-si")
    .factory("FacebookService", FacebookService);

  /** @ngInject */
  function FacebookService(Facebook) {

    var getLoginStatus = function(callback) {
      Facebook.getLoginStatus(function (response) {
        callback(response);
      });
    };

    var login = function(callback) {
      Facebook.login(function (response) {
        callback(response);
      }, {scope: 'email, public_profile, user_location, user_birthday', return_scopes: true});
    };

    var me = function(callback) {
      Facebook.api('/me', {fields: 'email, name, location, birthday'}, function (response) {
          callback(response);
      });
    };

    var logout = function() {
      Facebook.logout();
    };

    return {
      getLoginStatus: getLoginStatus,
      login: login,
      me: me,
      logout: logout
    }
  }
})();



(function() {
  "use strict";

  angular
    .module("project-si")
    .factory("AuthService", AuthService);

  /** @ngInject */
  function AuthService($rootScope, $http, apiAddress) {
    var USER_NOTIFY = "USER";
    var MENU_NOTIFY = "MENU";

    var notifyUserChanged = function (user) {
      $rootScope.$broadcast(USER_NOTIFY, {
        user: user
      })
    };

    var onNotifyUserChanged = function ($scope, handler) {
      $scope.$on(USER_NOTIFY, function (event, notify) {
        handler(notify.user)
      })
    };

    var openAuthModal = function (menu) {
      $rootScope.$broadcast(MENU_NOTIFY, {
        menu: menu
      })
    };

    var onOpenAuthModal = function ($scope, handler) {
      $scope.$on(MENU_NOTIFY, function (event, notify) {
        handler(notify.menu)
      })
    };

    var registerUser = function(user) {
      return $http.post(apiAddress + "/signup", user);
    };

    var login = function (auth) {
      return $http.post(apiAddress + "/auth", auth);
    };

    var logout = function () {
      return $http.post(apiAddress + "/logout");
    };

    return {
      notifyUserChanged: notifyUserChanged,
      onNotifyUserChanged: onNotifyUserChanged,
      openAuthModal: openAuthModal,
      onOpenAuthModal: onOpenAuthModal,
      register: registerUser,
      login: login,
      logout: logout
    };
  }
})();

(function () {
  "use strict";

  angular
    .module("project-si")
    .controller("AuthController", AuthController);

  /** @ngInject */
  function AuthController($scope, $location, toastr, AuthService, UserService, SessionService) {

    var vm = this;

    var AUTH_MODAL = "#auth-modal";

    AuthService.onNotifyUserChanged($scope, function(user) {
      vm.user = user;
      SessionService.setAuthenticatedUser(vm.user);
    });

    AuthService.onOpenAuthModal($scope, function (menu) {
      vm.showAuthModal(menu);
    });

    var redirectToAccountPage = function () {
      if (vm.user.matriculation === null) {
        angular.element("#navBarItems li a").each(function(){
          angular.element(this).removeClass("navbar-active");
        });
        $location.path("/account");
      }
      angular.element(AUTH_MODAL).hide("slide");
    };

    vm.user = SessionService.getAuthenticatedUser();
    vm.authActiveTab = undefined;
    vm.auth = {};
    vm.newUser = {};

    vm.showAuthModal = function (menu) {
      vm.authActiveTab = menu;
      angular.element(AUTH_MODAL).show("slide");
    };

    vm.closeAuthModal = function () {
      vm.authActiveTab = undefined;
      angular.element(AUTH_MODAL).hide("slide");
    };

    vm.login = function (auth) {
      AuthService.login(auth)
        .then(function(result) {
          vm.user = result.data;
          SessionService.setAuthenticatedUser(vm.user);
          redirectToAccountPage();
          vm.auth = {};
        }, function (error) {
          if(error.status === 401) {
            toastr.error("Email ou senha invalida", "Erro");
          } else {
            toastr.error("Tente novamente, se o erro pesistir entre em contato com o administrador", "Erro");
          }
        });
    };

    vm.register = function (user) {
      user.role = "USER";
      UserService.createUser(user)
        .then(function(result) {
          vm.user = result.data;
          var auth = {
            login: user.email,
            password: user.password
          };
          vm.login(auth);
        }, function(error) {
            toastr.error(error.data.message);
        });
    };

    vm.logout = function() {
      vm.user = undefined;
      SessionService.clear();
      AuthService.logout();
    };
  }
})();

